.App {
  height: 100vh;
  width: 100%;
  background-color: #444;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.App h1 {
  margin: 0;
  color: cornsilk;
  font-size: 10em;
  font-family: 'Monoton', cursive;
}

.buttons-container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 300px;
  margin-top: 1em;
  /* background-color: #fff1; */
  padding: 1em;
  border-radius: 5px;
}

button {
  flex: 0.9;
  font-family: 'Orbitron', sans-serif;
}

button:hover {
  cursor: pointer;
}